    height:1.2rem;
    background: #fff;
    font:0.28rem/1.2rem "\5FAE\8F6F\96C5\9ED1";
    color:#9A9A9A;
    padding: 0 4%;
}

.main-sDetails{
    margin: 1rem 0;
    width: 100%;
    height: 100%;
    position: relative;
    background: #F3f3f3;
}
.main-sDetails section{
    margin-bottom: 0.25rem;
    background: #fff;
    padding: 0.2rem;
}
.foot-sDetails{
    height: 1rem;
    width: 100%;
    background: rgb(196, 27, 27);
    z-index: 9;
    position: fixed;
    bottom: 0;
    left: 0;
}
.foot-sDetails h1{
    font: 0.35rem/1rem "\5FAE\8F6F\96C5\9ED1";
    text-align: center;
    font-weight: bold;
    color:#fff;
}
.sec1 dl{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.sec1 dl dt{
    width: 30%;
}
.sec1 dl dd{
    padding-left: 0.3rem;
}
.sec1 dl dd h2{
    font:0.34rem/0.5rem "\5FAE\8F6F\96C5\9ED1";
    font-weight: bold;
}
.sec1 dl dd div{
    font:0.33rem/1rem "\5FAE\8F6F\96C5\9ED1";
    font-weight: bold;
    color:#f00;
    position: relative;
    top:0.3rem;
}
.sec1 dl dd div span{
    color:orange;
    border:1px solid orange;
    font: 0.22rem/0.22rem "\5FAE\8F6F\96C5\9ED1";
}
.sec1 dl dd div img{
    position: absolute;
    right:0;
    bottom:0.3rem;
}
.sec2 p:nth-of-type(1){
    font:0.25rem/0.7rem "\5FAE\8F6F\96C5\9ED1";
    border-bottom:1px solid #eeeeee;
}
.sec2 p:nth-of-type(1) img{
    position: relative;
    top: 0.2rem;
    padding-right: 0.1rem;
}
.sec2 p:nth-of-type(2){
    position: relative;
}
.sec2 p:nth-of-type(2) img{
    position: absolute;
    top: 0.1rem;
    left: 0;
}
.sec2 p:nth-of-type(2) span{
    display:block;
    margin: 0.1rem 0 0 0.4rem;
}
.sec2 p:nth-of-type(2) span:nth-of-type(2){
    font: 0.2rem/0.2rem "\5FAE\8F6F\96C5\9ED1";
    color:rgb(201, 198, 198);
}
.sec2 p:nth-of-type(2) span:nth-of-type(3){
    position: absolute;
    right: 0.1rem;
    bottom: 0.1rem;
    font-size:0.35rem;
    font-weight: bold;
    color:#999;
    font-family: "\5FAE\8F6F\96C5\9ED1";
}
.sec-reset{
}
.sec-reset dl dt{
    font:0.29rem/0.5rem "\5FAE\8F6F\96C5\9ED1";
    color:#333;
    font-weight: bold;
    border-bottom:1px solid #eee;
    padding-bottom:0.2rem;
}
.sec-reset dl dd{
    width: 100%;
    overflow: hidden;
}
.sec-reset dl dd img{
    width: 100%;
    margin-left: -0.02rem;
}
.sec-reset dl dd p{
    padding-top: 0.1rem;
    font:0.25rem/0.4rem "\5FAE\8F6F\96C5\9ED1";
}

header{
    position: fixed;
    top:0;
    left:0;
}
.main-fir-details{
        padding: 0.2rem 0.3rem;
        margin: 1rem 0;
}
.main-fir-details dl dt h2{
        font:0.37rem/0.65rem "\5FAE\8F6F\96C5\9ED1";
        font-weight: bold;
}
.main-fir-details dl dt p{
        font:0.23rem/0.5rem "\5FAE\8F6F\96C5\9ED1";
        color:#888;
        position:relative;
        height:0.5rem;
        margin-bottom: 0.2rem;
}
.main-fir-details dl dt p span{
        position: absolute;
}
.main-fir-details dl dt p span:nth-of-type(2){
        right:0rem;
        top: 0;
}
.main-fir-details dl dd p{
        font:0.25rem/0.4rem "\5FAE\8F6F\96C5\9ED1";
        color:#333;
        text-align: justify;
}
.main-fir-details dl dd img{
        width: 100%;
        margin: 0.2rem 0;
}
.share dt{ 
        width: 100%;
        text-align: center;
        font:0.25rem/0.5rem "\5FAE\8F6F\96C5\9ED1";
        color:#888;
}
.share dt img{
        width: 1rem;
}
.share dt{
        margin: 0.3rem 0 0;
}
.share dt p{
        position: relative;
}
.share dd ul{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
}
.share dd ul li{
        text-align: center;
}
.share dd ul li img{
        width:0.5rem;
        height:0.5rem;
        border:1px solid #999;
        border-radius: 0.1rem;
        padding: 0.1rem;
}
.share dd ul li p{
        font:0.22rem/0.25rem "\5FAE\8F6F\96C5\9ED1";
        text-align: center;
}
.footer-fir-details{
        width: 100%;
        height: 0.8rem;
        position: fixed;
        bottom:0;
        left:0;
        background: #FFFFFF;
        border-top:1px solid #F1F1F1;
}
.footer-fir-details input{
        height: 0.5rem;
        width: 70%;
        border:none;
        margin: 0.2rem 0.2rem 0.1rem;
        border:1px solid #F1F1F1;
        border-radius:0.1rem;
        padding: 0rem 0.3rem;
        font:0.25rem/0.5rem "\5FAE\8F6F\96C5\9ED1";
}
.footer-fir-details img{
        width: 0.6rem;
        position: relative;
        top:0.2rem;
}




































